*,
*::after,
*::before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: hidden;
}

.nav-items{
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    right: 0;
    top: 0;
    list-style: none;
    z-index: 900;

    a{
        text-decoration: none;
        color: black;
        font-size: 3vmin;
        font-weight: 700;
    }
}

.home{
    background: #F7FDF6
    ;
}

.about{
    background: #8DB5CE;
}

.contact{
    background: #EC7F8E;
}

.home,
.about,
.contact{
    z-index: 1;
    height: 100vh; 
    width: 100%;
    position: absolute;
    display: grid;
    place-items: center;

    .home-content,
    .about-content{
        width: 80%;
        margin: 0 auto;

        h1{
            font-size: 10vmin;
        }

        h3{
            font-size: 6vmin;
        }

        p{
            font-size: 2vmin;
        }
    }
}